.about {
    background-color: var(--bg-color-top);
}

.about__container {
    padding-bottom: 60px;
    padding-top: 70px;
}

.about__wrapper {
    display: block;
    width: 463px;
}

.about__content-container {
    display: flex;
    align-items: center;
    margin-top: 82px;
}

.about__section {
    width: 463px;
    font-size: 14px;
    line-height: 1.94;
    letter-spacing: 0.04em;
}

.about__text-first {
    font-size: 16px;
    line-height: 1.76;
    margin-bottom: 8px;
}

.about__text-second {
    margin-bottom: 8px;
}

.about__button {
    margin-top: 48px;
}

.cow {
    width: 490px;
    height: 529px;
    margin-right: 100px;
    background-image: url("../images/about/about-cow-desktop.png"), linear-gradient(90deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7));
    box-shadow: 0px 8px 30px rgba(153, 114, 23, 0.25);
    border-radius: 250px;
    background-size: 490px;
    background-repeat: no-repeat;
    background-position: top 39px left, top left;
}