.modal-franchise__text {
    margin-bottom: 24px;
    margin-top: 20px; 
    width: 100%;
    line-height: 1.4;
    color: var(--text-color_dark);
   
    @include tablet {
        margin-top: 24px;
        margin-bottom: 32px;
        letter-spacing: normal;

    }

}

    .modal-franchise__title {
        width: 100%;
        letter-spacing: normal;
        color: var(--text-color_dark);
        @include tablet {
            width: 332px;
            text-align: center;
            margin-left: auto;
            margin-right: auto;
            
     
         }
        

        }
    
    
    .modal-form {
        display: flex;
        flex-direction: column;
        @include tablet {
           width: 368px;
           margin-left: auto;
           margin-right: auto;
    
        }
    }

    .modal-form__input {
      
    
        width: 100%;
        height: 32px;
        
        
        background: rgba(248, 210, 78, 0.1);
        border: 1px solid #84A178;
        border-radius: 10px;   
        padding-left: 12px;
    }
    
    .modal-form__label + .modal-form__label {
    margin-top: 12px;
    @include tablet {
        margin-top: 16px;
 
     }
    }
    
    
    .modal-form__message {
    
     padding: 8px 12px;
    
    width: 100%;
    height: 64px;
    
    background: rgba(248, 210, 78, 0.1);
    border: 1px solid #84A178;
    border-radius: 10px;
   
    }

    .modal-form__input::placeholder {
        font-size: 12px;
        line-height: 1.4;
        color: var(--text-color_dark);
    }

     
    .modal-location__btn,
    .modal-franchise__btn {
        margin-left: auto;
        margin-right: auto;
        margin-top: 24px;
        @include tablet {
            margin-top: 32px;
        }
    }
 
    
    
    
    .modal-location__title {
    display: flex;
   
    justify-content: center;
    
    color: var(--text-color_dark);
    margin-bottom: 20px;
    @include tablet {
        margin-bottom: 32px;
    }
   
    }
    
    
    
    .modal-location, .modal-franchise {
        @include tablet {
            padding: 32px 60px;
        }
    }
    
   
   

    

    .modal-form__message::placeholder {
      
        
        font-size: 12px;
        line-height: 1.4;
        color: var(--text-color_dark);
    } 

    .picture-location {
        
        width: 100%;
    }
