.modal {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1) rotate(0deg);
    max-width: 280px;
    padding: 28px;
    background-color: var(--bg-color-modal);
    color: var(--text-color_dark);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border: 2px solid var(--color-accent);
    border-radius: 10px;

    @include mobile {
        max-width: 280px;
    }

    @include tablet {
        max-width: 568px;

    }

}

