.modal__close {
    position: absolute;
    top: 8px;
    right: 8px;

    @include tablet {
        top: 20px;
        right: 20px;
    }

    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 20px;
    border: none;
    background-color: inherit;
    color: var(--text-color_dark);
    // transition: color var(--animetion-time-func);

    // filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

    &:hover,
    &:focus {
        color: var(--color-accent);
    }
}

